import React, { useState, useEffect } from "react";
import HTMLReactParser from "html-react-parser";
import { Link, useParams } from "react-router-dom";
import { Typography, Avatar, Row, Col, Select } from "antd";
import millify from "millify";
import {
    MoneyCollectOutlined,
    DollarCircleOutlined,
    FundOutlined,
    ExclamationCircleOutlined,
    StopOutlined,
    TrophyOutlined,
    CheckOutlined,
    NumberOutlined,
    ThunderboltOutlined
} from "@ant-design/icons";
import {
    useGetCryptoDetailsQuery,
    useGetCryptoHistoryQuery
} from "../services/cryptoApi";
import LineChart from "./lineChart";
import Loader from "./loader";

const { Title, Text } = Typography;
const { Option } = Select;
export default function CryptoDetails() {
    const { coinId } = useParams();
    const [timeperiod, setTimeperiod] = useState("7d");
    const { data, isFetching } = useGetCryptoDetailsQuery(coinId);
    const { data: coinHistory } = useGetCryptoHistoryQuery({
        coinId,
        timeperiod
    });

    const cryptoDetails = data?.data?.coin;

    const time = ["3h", "24h", "7d", "30d", "1y", "3m", "3y", "5y"];
    const stats = [
        {
            title: "Price to USD",
            value: `$ ${cryptoDetails?.price && millify(cryptoDetails?.price)}`,
            icon: <DollarCircleOutlined />
        },
        { title: "Rank", value: cryptoDetails?.rank, icon: <NumberOutlined /> },
        {
            title: "24h Volume",
            value: `$ ${cryptoDetails?.["24hVolume"] && millify(cryptoDetails?.["24hVolume"])
                }`,
            icon: <ThunderboltOutlined />
        },
        {
            title: "Market Cap",
            value: `$ ${cryptoDetails?.marketCap && millify(cryptoDetails?.marketCap)
                }`,
            icon: <DollarCircleOutlined />
        },
        {
            title: "All-time-high(daily avg.)",
            value: `$ ${cryptoDetails?.allTimeHigh?.price &&
                millify(cryptoDetails?.allTimeHigh?.price)
                }`,
            icon: <TrophyOutlined />
        }
    ];

    const genericStats = [
        {
            title: "Number Of Markets",
            value: cryptoDetails?.numberOfMarkets,
            icon: <FundOutlined />
        },
        {
            title: "Number Of Exchanges",
            value: cryptoDetails?.numberOfExchanges,
            icon: <MoneyCollectOutlined />
        },
        {
            title: "Aprroved Supply",
            value: cryptoDetails?.supply?.confirmed ? (
                <CheckOutlined />
            ) : (
                <StopOutlined />
            ),
            icon: <ExclamationCircleOutlined />
        },
        {
            title: "Total Supply",
            value: `$ ${cryptoDetails?.supply?.total && millify(cryptoDetails?.supply?.total)
                }`,
            icon: <ExclamationCircleOutlined />
        },
        {
            title: "Circulating Supply",
            value: `$ ${cryptoDetails?.supply?.circulating &&
                millify(cryptoDetails?.supply?.circulating)
                }`,
            icon: <ExclamationCircleOutlined />
        }
    ];

    if (isFetching) return <Loader />;

    return (
        <>
            <Col span={24} className="coin-detail-container">
                <Col className="coin-heading-container">
                    <Title level={2} className="coin-name">
                        {cryptoDetails?.name} ({cryptoDetails.symbol}) Price
                    </Title>
                    <p>
                        {cryptoDetails.name} live price in US dollars. View value
                        statistics, market cap and supply.
                    </p>
                </Col>
                <Select
                    defaultValue="7d"
                    placeholder="Select Timeperiod"
                    className="select-timeperiod"
                    onChange={(value) => setTimeperiod(value)}
                >
                    {time.map((date) => (
                        <Option key={date}>{date}</Option>
                    ))}
                </Select>

                <LineChart
                    coinHistory={coinHistory}
                    currentPrice={millify(cryptoDetails.price)}
                    coinName={cryptoDetails.name}
                />

                <Col className="stats-container">
                    <Col className="coin-value-statistics">
                        <Col className="coin-value=statistics-heading">
                            <Title level={3} className="coin-details">
                                {cryptoDetails?.name} Value Statistics
                            </Title>
                            <p>An overview showing the stats of {cryptoDetails?.name}</p>
                        </Col>
                        {stats.map(({ icon, title, value }) => (
                            <Col className="coin-stats" key={value}>
                                <Col className="coin-stats-name">
                                    <Text>{icon}</Text>
                                    <Text>{title}</Text>
                                </Col>
                                <Text>{value}</Text>
                            </Col>
                        ))}
                    </Col>
                    <Col className="other-stats-info">
                        <Col className="coin-value=statistics-heading">
                            <Title level={3} className="coin-details">
                                Other Statistics
                            </Title>
                            <p>An overview showing the stats of all cryptocurrencies.</p>
                        </Col>
                        {stats.map(({ icon, title, value }) => (
                            <Col className="coin-stats" key={value}>
                                <Col className="coin-stats-name">
                                    <Text>{icon}</Text>
                                    <Text>{title}</Text>
                                </Col>
                                <Text>{value}</Text>
                            </Col>
                        ))}
                    </Col>
                </Col>

                <Col className="coin-desc-link">
                    <Row className="coin-desc">
                        <Title level={3} className="coin-details-heading">
                            What is {cryptoDetails?.name}?
                        </Title>
                        <p>{HTMLReactParser(cryptoDetails.description)}</p>
                    </Row>
                    <Col className="coin-links">
                        <Title level={3} className="coin-details-heading">
                            {cryptoDetails?.name} Links
                        </Title>
                        {cryptoDetails.links.map((link) => (
                            <Row className="coin-link" key={link.name}>
                                <Title className="link-name" level={5}>
                                    {link.type}
                                </Title>
                                <a href={link.url} target="_blank">
                                    {link.name}
                                </a>
                            </Row>
                        ))}
                    </Col>
                </Col>
            </Col>
        </>
    );
}
